  --bs-code-color: #e83e8c;
  --bs-highlight-color: #222;
  --bs-highlight-bg: rgb(250.6, 232.8, 204.4);
  --bs-border-width: 1px;
  --bs-border-style: solid;
  --bs-border-color: #dee2e6;
  --bs-border-color-translucent: rgba(0, 0, 0, 0.175);
  --bs-border-radius: 0;
