  --bs-tertiary-bg: rgb(42.5, 42.5, 42.5);
  --bs-tertiary-bg-rgb: 43, 43, 43;
  --bs-primary-text-emphasis: rgb(102, 186, 213.6);
  --bs-secondary-text-emphasis: rgb(244.8, 244.8, 244.8);
  --bs-success-text-emphasis: rgb(142.2, 205.2, 165.6);
